<template>
  <div class="book-preview">
    <router-link :to="'/book/' + book.id">
      <h1>{{ book.title }}</h1>
      <img :src="book.thumbnail" alt="" />
      <p>{{book.listPrice.amount}}{{getCurr}}</p>
    </router-link>
    <div class="actions">
      <button @click.prevent="remove">Remove</button>
    </div>
  </div>
</template>
<script>
export default {
  props: ['book'],
  methods: {
    remove() {
        console.log('ddd')
      this.$emit('remove', this.book.id);
    },
  },
  computed:{
        getCurr() {
      if (this.book.listPrice.currencyCode === 'ILS') return '₪';
      if (this.book.listPrice.currencyCode === 'EUR') return '€';
      if (this.book.listPrice.currencyCode === 'USD') return '$';
      return '$';
    },
  }
};
</script>
