<template>
  <section>
    <h1>Book App</h1>
    <book-filter @filtered="setFilter" />
    <book-list :books="booksToShow" @remove="onRemove" />
  </section>
</template>
<script>
import BookList from '../cmps/BookList.vue';
import BookFilter from '../cmps/BookFilter.vue';
export default {
  data() {
    return {
      books: [],
      filterBy: null,
    };
  },
  created() {
    // this.loadBooks();
  },

  components: {
    BookList,
    BookFilter,
  },
  methods: {

    async loadBooks() {
      await this.$store.dispatch({ type: 'loadBooks' });
    },

    async onRemove(bookId) {
      await this.$store.dispatch({ type: 'removeBook', bookId });
    },

    setFilter(filterBy) {
      this.$store.commit({ type: 'setFilter', filterBy });
    },
  },
  computed: {
    booksToShow() {
      return this.$store.getters.booksToShow;
    },
  },
};
</script>
