import { bookService } from '../../services/bookService'

export default {
    state: {
        books: null,
        filterBy: null,
        selectedBook: null
    },
    mutations: {
        setBooks(state, payload) {
            state.books = payload.books
        },
        setFilter(state, { filterBy }) {
            state.filterBy = filterBy
        },
        removeBook(state, { bookId }) {
            const idx = state.books.findIndex(book => book.id === bookId)
            state.books.splice(idx, 1)
        },
        saveBook(state, { book }) {
            const idx = state.books.findIndex(currBook => currBook.id === book.id)
            if (idx === -1) state.books.push(book)
            else state.books.splice(idx, 1, book)
        },
        setSelectedBook(state, { book }) {
            state.selectedBook = book
        }
    },
    actions: {
        async loadBooks(context) {
            try{
                const books = await bookService.query()
                context.commit({ type: 'setBooks', books })
            }
            catch(err){
                console.log('err with loading books',err)
            }
           
        },
        async removeBook(context, { bookId }) {
            try {
                await bookService.remove(bookId)
                context.commit({ type: 'removeBook', bookId })
                context.dispatch({ type: 'setMsg', msgStr: 'Book removed' })
            } catch (err) {
                context.dispatch({ type: 'setMsg', msgStr: 'book remove failed' })
                console.log(err);
            }
        },
        async saveBook(context, { book }) {
            try{
                
                const savedBook = await bookService.add(book)
                context.commit({ type: 'saveBook', book: savedBook })
                context.dispatch({ type: 'setMsg', msgStr: 'Google Book Added' })
            }
            catch(err){
                console.log('book add failed ',err)
                context.dispatch({ type: 'setMsg', msgStr: 'Book add failed' })
            }
           
        },
        async getbookById(context, { bookId }) {
            try{
                const book = await bookService.get(bookId)
                context.commit({ type: 'setSelectedBook', book })
            }
            catch(err){
                console.log('error with loading book',err)
            }
           
        }
    },
    getters: {
        booksToShow(state) {
            const { books } = state;
            const { filterBy } = state;
            const minPrice = filterBy?.minPrice || -Infinity;
            const maxPrice = filterBy?.maxPrice || Infinity;
            if (!filterBy) return books;
            let filter = filterBy;
            const regex = new RegExp(filter.txt, 'i');
            const filteredBooks = books.filter((book) => {
                return (
                    regex.test(book.title) &&
                    book.listPrice.amount > minPrice &&
                    book.listPrice.amount < maxPrice
                );
            });
            // filterBy = null;
            return filteredBooks;
        },
        selectedBook(state) {
            return state.selectedBook
        },
        books(state) {
            return state.books
        }
    }
}