<template>
  <section class="review-add flex column">
    <h1>Add new review</h1>
    <form>
      <el-input placeholder="Full name" v-model="review.name"></el-input>
      <div class="flex">
        <el-date-picker
          v-model="review.date"
          type="date"
          placeholder="Pick a day"
        >
        </el-date-picker>
      </div>
      <el-input
        type="textarea"
        :autosize="{ minRows: 4, maxRows: 6 }"
        placeholder="Your review here..."
        v-model="review.txt"
      ></el-input>
      <el-rate v-model="review.rate"></el-rate>
      <button class="btn-submit" @click.prevent="add">
        Submit
      </button>
    </form>
  </section>
</template>
<script>
export default {
  data() {
    return {
      review: {
        name: '',
        txt: '',
        rate: null,
        date: '',
      },
    };
  },
  methods: {
    add() {
      this.$emit('addReview', this.review);
      this.review = {
        name: '',
        txt: '',
        rate: null,
        date: '',
      };
    },
  },
};
</script>
