<template>
  <h1 v-if="!book">Loading...</h1>
  <section v-else class="book-details-container">
    <h1>{{ book.title }}</h1>
    <p v-show="book.subtitle">{{ book.subtitle }}</p>
    <img :src="book.thumbnail" alt="" />
    <p v-for="author in book.authors" :key="author">{{ author }}</p>
    <span>category: </span>
    <p v-for="category in book.categories" :key="category">{{ category }}</p>
    <p>{{ description }}</p>
    <p>language: {{ book.language }}</p>
    <p :class="price">{{ book.listPrice.amount }}{{ getCurr }}</p>
    <p>publishedDate: {{ book.publishedDate }}</p>
    <p>{{ pageCount }}</p>
    <p>{{ bookAge }}</p>
    <h2>{{ sale }}</h2>
    <button @click="goBack">Go back</button>
    <div class="btn-navigate flex space-around">
      <el-button
        @click="onChangePage(-1)"
        type="secondry"
        icon="el-icon-arrow-left"
        >Previous Book</el-button
      >

      <el-button @click="onChangePage(1)" type="secondry"
        >Next Book<i class="el-icon-arrow-right"></i
      ></el-button>
    </div>

    <review-add @addReview="onAddReview" />
    <review-list :reviews="book.reviews" @remove="onRemove" />
  </section>
</template>
<script>
import { bookService } from '@/services/bookService.js';
import { reviewService } from '@/services/reviewService.js';
import ReviewAdd from '../cmps/ReviewAdd.vue';
import ReviewList from '../cmps/ReviewList.vue';
export default {
  components: { ReviewAdd, ReviewList },
  data() {
    return {
      book: null,
      desc: '',
    };
  },
  created() {
    this.loadBook();
  },
  watch: {
    '$route.params.id'(newVal, oldVal) {
      if (newVal !== oldVal) {
        console.log('watch');
        this.loadBook();
      }
    },
  },
  methods: {
    goBack() {
      this.$router.push('/book');
    },
    onChangePage(diff) {
      const { id } = this.book;
      const books = this.$store.getters.books;
      console.log(books, 'details');
      const idx = books.findIndex((currbook) => currbook.id === id);
      const newBookId = books[idx + diff].id;
      if (!newBookId) return;
      this.$router.push('/book/' + newBookId);
    },

    async onRemove(reviewId) {
      await reviewService.removeReview(reviewId, { ...this.book });
      this.loadBook();
    },
    async onAddReview(review) {
      await reviewService.addReview({ ...review }, this.book.id);
      this.loadBook();
    },
    async loadBook() {
      console.log('Load');
      const { id } = this.$route.params;
      const book = await bookService.getBookById(id);
      this.book = book;
      console.log(book);
    },
  },

  computed: {
    description() {
      if (!this.book) return;
      const description = this.book.description?.substring(0, 100);
      this.desc = description;
      return description;
    },
    getCurr() {
      if (this.book.listPrice.currencyCode === 'ILS') return '₪';
      if (this.book.listPrice.currencyCode === 'EUR') return '€';
      if (this.book.listPrice.currencyCode === 'USD') return '$';
      return '$';
    },
    pageCount() {
      if (this.book.pageCount > 500) return 'Long reading';
      if (this.book.pageCount > 200) return 'Decent Reading';
      if (this.book.pageCount < 100) return 'Light Reading';
    },
    bookAge() {
      const currYear = new Date(Date.now()).getFullYear();
      if (currYear - this.book.publishedDate > 10) return 'Veteran Book';
      if (currYear - this.book.publishedDate === 1) return 'New Book';
    },
    price() {
      if (this.book.listPrice.amount > 150) return 'high-price';
      if (this.book.listPrice.amount < 20) return 'low-price';
    },
    sale() {
      if (this.book.listPrice.isOnSale) return 'Sale !!!';
    },
  },
};
</script>
