<template>
    <section class="review-preview">
        <p>{{review.name}}</p>
        <p>{{review.date| moment("DD/MM/YY")}}</p>
        <p>{{review.txt}}</p>
        <el-rate v-model="review.rate" disabled text-color="#ff9900"> </el-rate>
        <button @click="remove">Remove</button>
    </section>
</template>
<script>
export default {
    props:['review'],
    methods: {
        remove(){
            this.$emit('remove',this.review.id)
        }
    },
}
</script>
