<template>
    <section >
            <h1>Review List </h1>
            <ul class="review-list-container">
                <li v-for="review in reviews" :key="review.id">
                    <review-preview :review="review" @remove="remove"/>
                </li>
            </ul>
    </section>
</template>
<script>
import ReviewPreview from './ReviewPreview.vue'
export default {
  components: { ReviewPreview },
    props:['reviews'],
    methods: {
        remove(reviewId){
            this.$emit('remove',reviewId)
        }
    },
}
</script>