<template>
  <section class="add-gbooks">
    <h1>AddGoogleBook</h1>
    <label>
      <input
        type="text"
        name="googleSearch"
        placeholder="Search book from google"
        @input="getBooks"
        v-model="query"
      />
    </label>
    <ul>
      <li v-for="book in books" :key="book.id">
        <span>{{ book.volumeInfo.title }}</span>
        <button @click="addBook(book)">Add to Library</button>
      </li>
    </ul>
  </section>
</template>

<script>
import { googleApiService } from '../services/googleApiService';
export default {
  data() {
    return {
      books: null,
      query:''
    };
  },
  methods: {
    async getBooks() {
      const books = await googleApiService.getBooksFromGoogle(this.query);
      this.books = books;
    },
    async addBook(book) {
      const addedBook = await this.$store.dispatch({
        type: 'saveBook',
        book: book.volumeInfo,
      });
      this.$router.push('/book');
    },
  },
};
</script>
