<template>
  <section>
    <ul class="book-list">
      <li v-for="book in books" :key="book.id" >
        <book-preview :book="book" @remove="remove" />
      </li>
    </ul>
  </section>
</template>
<script>
import BookPreview from './BookPreview.vue';
export default {
  components: { BookPreview },
  props: ['books'],
  methods: {
    remove(bookId) {
        console.log(bookId)
      this.$emit('remove', bookId);
    },
  },
};
</script>
