<template>
  <section>
    <user-msg :msgStr="userMsgStr" v-show="userMsgStr" />
    <app-header />
    <router-view />
  </section>
</template>

<script>
import appHeader from './cmps/AppHeader';
import UserMsg from '@/cmps/UserMsg';
export default {
  components: {
    appHeader,
    UserMsg,
  },
  created(){
   this.loadBooks()
  },
  methods: {
    async loadBooks() {
      await this.$store.dispatch({ type: 'loadBooks' });
    },
  },
  computed: {
    userMsgStr() {
      return this.$store.getters.userMsg;
    },
  },
};
</script>
