import { bookService } from './bookService';
export const reviewService = {
    getReviewsByBookId, addReview, removeReview
}
async function getReviewsByBookId(bookId) {
    try {
        const currBook = await bookService.getBookById(bookId);
        return currBook.reviews;
    }
    catch (err) {
        console.log('err is:', err);
    }
}

async function addReview(review, bookId) {
    console.log(review, bookId)
    review.id = makeId()
    try {
        const book = await bookService.getBookById(bookId)
        const copyBook = { ...book }
        if (!copyBook.reviews) copyBook.reviews = [];
        copyBook.reviews.push(review)
        bookService.updateBook(copyBook);
    }
    catch (err) {
        console.log('err with add review', err)
    }
}

async function removeReview(reviewId, book) {
    console.log(reviewId, book)
    try {
        const idx = book.reviews.findIndex((review) => review.id === reviewId)
        const reviewsCopy = [...book.reviews]
        reviewsCopy.splice(idx, 1)
        console.log(reviewsCopy)
        book.reviews = reviewsCopy;
        await bookService.updateBook(book)
    }
    catch (err) {
        console.log('error with remove review', err)
    }
}

function makeId() {
    var ALPHABET = '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ';
    var ID_LENGTH = 8;
    var rtn = '';
    for (var i = 0; i < ID_LENGTH; i++) {
        rtn += ALPHABET.charAt(Math.floor(Math.random() * ALPHABET.length));
    }
    return rtn;
}