<template>
  <div>
    <input
      type="text"
      placeholder="Search"
      v-model="filterBy.txt"
      @input="filtered"
    />
    <input
      type="text"
      placeholder="Min price"
      @input="filtered"
      v-model.number="filterBy.minPrice"
      class="center-input"
    />
    <input
      type="text"
      placeholder="Max price"
      @input="filtered"
      v-model.number="filterBy.maxPrice"
    />
  </div>
</template>
<script>
export default {
  data() {
    return {
      filterBy: {
        txt: '',
        minPrice: null,
        maxPrice: null,
      },
    };
  },
  methods: {
    filtered() {
      this.$emit('filtered', { ...this.filterBy });
    },
  },
};
</script>
