import Axios from 'axios'

export const googleApiService = {
    getBooksFromGoogle
}




async function getBooksFromGoogle(filterSearch) {
    // const books = storageService.load('gbooks')
    // if (books) return books;
    try {
        const url = `https://www.googleapis.com/books/v1/volumes?q=${filterSearch}&maxResults=40`;
        const res = await Axios.get(url)
        console.log(res.data.items)
        // storageService.save('gbooks',res.data.items)
        return res.data.items
    }
    catch (err) {
        console.log('faild to retrieveing books from google', err)
    }


}